import React, { useEffect,useState } from "react";
import { useLanguage } from '../components/LanguageContext';
import { faArrowRight , faLocationDot , faXmark , faChevronRight , faChevronLeft} from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

import ideaIcon from '../assets/ideaIcon.png';
import frontTemple from '../assets/frontTemple2.png';
import antiques4 from '../assets/antiques5.png';
import { height } from "@fortawesome/free-brands-svg-icons/fa42Group";

const AboutPage = () => {
    const { language } = useLanguage('th');
    const [isMobile, setIsMobile] = useState(false);

    useEffect(() => {
        window.addEventListener('resize', handleResize);
        handleResize(); // Initial check
        return () => window.removeEventListener('resize', handleResize);
      }, []);
    
      const handleResize = () => {
        setIsMobile(window.innerWidth <= 1200);
      };
    

    const styles = {
        Page: {
            paddingTop:isMobile ? '100px' : '150px',
            width: '100%',          // Change width to 100% to use the full screen width
            boxSizing: 'border-box',
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center',
            margin: '0 auto',  // Center content horizontally
            gap:'16px',
           
        },
        content: {
            display: 'flex',
            flexDirection: 'row',  // Make sure content is in a row
            width:isMobile ? '100%' : '95%',  // Make content fill the width of its parent container
            justifyContent: 'space-between', // Distribute space between the left and right content
            flexDirection: isMobile ? 'column' : 'row',
            padding: isMobile ? '0px 16px' : '0px',
            boxSizing:'border-box'
        },
        Title:{
            fontFamily:  language === 'en' ? "'Cormorant Garamond', serif" :  "'Kanit', sans-serif",
            color:'#2e5853',
            fontSize:'48px',
            margin:0,
            width: '100%' // Ensure title spans full width
        },
        Details:{
            color:'#666666',
            fontFamily:  language === 'en' ?  "'Poppins', sans-serif" :  "'Kanit', sans-serif",
            justifyContent: 'center', 
            width: '100%'  // Make sure text section is not constrained
        },
        contentLeft:{
            display:'flex',
            textAlign:'left',
            width:isMobile ? '100%' :'55%',  // Allow left content to take up 48% of the width
            flexDirection: 'column',
            boxSizing:'border-box'
        },
        design:{
            borderTop: '1px solid #AD9468',
            gap:'24px',
            fontSize:isMobile ? '12px' :'24px',
        },
        contentRight:{
            borderRadius:'30px',
            padding:'24px 16px',
            background:'#2e5853',
            display: 'flex',
            alignItems: 'center', 
            flexDirection: 'column',   
            textAlign: 'left',
            gap:'10px',
            width:isMobile ? '100%' :'38%', 
            boxSizing:'border-box',
            marginTop :isMobile ? '24px' :'0',
        },
        imgAbout:{
            width:isMobile ? '45%' : '50%',
            borderRadius:'18px'
        }
    }

    return(
        <div style={styles.Page}>
            <div style={styles.content}>
                <div style={styles.contentLeft}>
                    <div style={styles.Details}>
                        {language === 'en' ? (
                            <>
                                <p style={styles.Title}>What we do ?</p>
                                <div style={styles.design}>
                                    <p>Experience a virtual journey through <strong style={{color:'#AD9468'}}>Virtual Tours</strong>, taking you to two <br />
                                    culturally and historically rich destinations at Bang Kachao.</p>
                                    <p>Start with <strong style={{color:'#AD9468'}}>Wat Protket Chetiyaram</strong>, where you can pay homage to <br />
                                    the Reclining Buddha for blessings and a turn of fortune. Admire the <br />
                                    sacred waterfront mondop (pavilion), and conclude your journey with <br />
                                    a visit to the <strong style={{color:'#AD9468'}}>Klong Bon Folk Museum</strong>, a treasure trove of rare <br />
                                    antiques preserving cultural heritage for future generations.</p>
                                </div>
                            </>
                        ) : (
                            <>
                                <p style={styles.Title}>เราทำอะไร ?</p>
                                <div style={styles.design}>
                                    <p>สัมผัสประสบการณ์ท่องเที่ยวเสมือนจริงผ่าน <strong style={{color:'#AD9468'}}>Virtual Tours</strong>, ที่จะพาคุณเดินทาง <br />
                                    ไปยังสองสถานที่สำคัญที่เต็มไปด้วยวัฒนธรรมและประวัติศาสตร์ที่คุ้มบางกะเจ้า</p>
                                    <p>เริ่มต้นด้วย<strong style={{color:'#AD9468'}}>วัดโปรดเกศเชษฐาราม</strong>, ร่วมสักการะพระพุทธไสยาสน์หรือพระนอน <br />
                                    เพื่อเสริมสิริมงคลและพลิกดวงชะตาจากร้ายกลายเป็นดี <br />
                                    พร้อมชมพระมณฑปกลางน้ำอันศักดิ์สิทธิ์ และปิดท้ายด้วยการ <br />
                                    เยี่ยมชม <strong style={{color:'#AD9468'}}>พิพิธภัณฑ์พื้นบ้านคลองบน</strong>,แหล่งรวบรวมของโบราณหายากเพื่อสืบทอดมรดก <br />
                                    ทางวัฒนธรรมให้คนรุ่นหลัง</p>
                                </div>
                            </>
                        )}
                    </div>
                    <div style={{ 
                        display: 'flex', 
                        gap: '16px', 
                        justifyContent: 'center', 
                        alignItems: 'center' 
                    }}> 
                        <img src={frontTemple} alt="frontTemple" style={styles.imgAbout} />
                        <img src={antiques4} alt="antiques4" style={styles.imgAbout} />
                    </div>

                </div>
                <div style={styles.contentRight}>
                <img 
                    src={ideaIcon} 
                    alt="ideaIcon" 
                    style={{
                        width:'30%',
                        marginBottom: '16px'  // Add space below the image
                    }}
                />
                {language === 'en' ? (
                    <p style={{
                        color:'#ffe095',
                        fontFamily:  language === 'en' ? "'Cormorant Garamond', serif" :  "'Kanit', sans-serif",
                        fontSize:isMobile ? '25px' :'35px',
                        margin: 0,
                        gap:'4px',
                        borderBottom:'1px solid #ffe095',
                        textAlign: 'left',  // Ensure left alignment
                        display: 'flex',  // Use flexbox to ensure the icon and text are aligned in a row
                        alignItems: 'center',  // Center the icon vertically
                        justifyContent: 'center',  // Align the content to the left within the container
                    }}>
                        Social + Innovation  
                        <FontAwesomeIcon 
                            icon={faArrowRight} 
                            style={{ fontSize: '18px', padding:'0 4px' }}
                        />
                        Sustainability
                    </p>
                ) : (
                    <p style={{
                        color:'#ffe095',
                        fontFamily:  language === 'en' ? "'Cormorant Garamond', serif" :  "'Kanit', sans-serif",
                        fontSize:isMobile ? '25px' :'35px',
                        margin: 0,
                        gap:'4px',
                        borderBottom:'1px solid #ffe095',
                        textAlign: 'left',  // Ensure left alignment
                        display: 'flex',  // Use flexbox to ensure the icon and text are aligned in a row
                        alignItems: 'center',  // Center the icon vertically
                        justifyContent: 'center',  // Align the content to the left within the container
                        
                    }}>
                        สังคม + นวัตกรรม  
                        <FontAwesomeIcon 
                            icon={faArrowRight} 
                            style={{ fontSize: '18px', padding:'0 4px' }}
                        />
                        ความยั่งยืน
                    </p>
                )}

                {language === 'en' ? (
                    <p style={{
                        fontSize:isMobile ? '12px' :'20px',
                        color:'white',
                        textAlign:'left',  // Ensure text is aligned to the left
                        fontFamily:  language === 'en' ?  "'Poppins', sans-serif" :  "'Kanit', sans-serif",
                    }}>
                        Join us and become a part of our mission to preserve
                        the natural beauty and cultural heritage of Khung
                        Bang Kachao. By supporting local businesses and
                        communities, you're helping to ensure a sustainable
                        future for this unique region. Experience the joy of
                        giving back while exploring this hidden gem of Thailand.
                    </p>
                ) : (
                    <p style={{
                        fontSize:isMobile ? '12px' :'20px',
                        color:'white',
                        textAlign:'left',  // Ensure text is aligned to the left
                        fontFamily:  language === 'en' ?  "'Poppins', sans-serif" :  "'Kanit', sans-serif",
                        

                    }}>
                        ร่วมกับเราและเป็นส่วนหนึ่งของภารกิจในการอนุรักษ์ความงามทางธรรมชาติ
                        และมรดกทางวัฒนธรรมของพื้นที่บางกระเจ้า
                        โดยการสนับสนุนธุรกิจท้องถิ่นและชุมชน
                        คุณกำลังช่วยให้มั่นใจถึงอนาคตที่ยั่งยืนสำหรับภูมิภาคที่มีเอกลักษณ์นี้
                        สัมผัสความสุขในการให้กลับในขณะที่สำรวจอัญมณีที่ซ่อนเร้นของประเทศไทย
                    </p>
                )}
            </div>

            </div>
        </div>
    );
};

export default AboutPage;
