import React, { useState } from "react";
import { motion } from "framer-motion";
import bucketImage from "../assets/seemsee.png";
import stickImage from "../assets/stickSeemsee.png";

const GameSeemsee = () => {
  const [stickNumber, setStickNumber] = useState(null); // เก็บเลขที่สุ่มได้
  const [showBucket, setShowBucket] = useState(false); // แสดง/ซ่อนถังเซียมซี
  const [showStick, setShowStick] = useState(false); // แสดง/ซ่อนไม้เซียมซี
  const [isButtonVisible, setIsButtonVisible] = useState(true); // แสดง/ซ่อนปุ่ม

  // ฟังก์ชันสุ่มเลขเซียมซี
  const drawFortuneStick = () => {
    const randomNum = Math.floor(Math.random() * 30) + 1; // เลข 1-30
    setStickNumber(randomNum);
  };

  // ฟังก์ชันเริ่มอนิเมชั่น
  const startAnimation = () => {
    setIsButtonVisible(false); // ซ่อนปุ่มเมื่อกด
    setShowBucket(false); // ซ่อนถังก่อนเริ่มใหม่
    setShowStick(false); // ซ่อนไม้ก่อนเริ่มใหม่
    setStickNumber(null); // ล้างเลขเก่า

    // เริ่มเฟดถังเซียมซีเข้ามา
    setTimeout(() => {
      setShowBucket(true);
      // หลังจากถังแสดงเสร็จ ให้แสดงไม้เซียมซี
      setTimeout(() => {
        setShowStick(true);
        drawFortuneStick(); // สุ่มเลข
      }, 1500); // Delay 1.5 วินาทีหลังถังแสดง
    }, 500); // Delay 0.5 วินาทีหลังปุ่มกด
  };

  return (
    <div style={{ textAlign: "center", marginTop: "20px" }}>
      {/* ปุ่มเริ่ม */}
      {isButtonVisible && (
        <button
          onClick={startAnimation}
          style={{
            padding: "10px 20px",
            fontSize: "18px",
            cursor: "pointer",
            margin: "20px",
          }}
        >
          start
        </button>
      )}

      {/* ถังเซียมซี */}
      {showBucket && (
        <motion.div
          initial={{ opacity: 0 }} // เริ่มด้วยความโปร่งใส
          animate={{ opacity: 1 }} // เฟดเข้ามา
          transition={{ duration: 1.5 }} // ระยะเวลาการเฟด
          style={{ position: "relative", display: "inline-block" }}
        >
          <img src={bucketImage} alt="ถังเซียมซี" width="200px" />

          {/* ไม้เซียมซี */}
          {showStick && (
            <motion.img
              src={stickImage}
              alt="ไม้เซียมซี"
              initial={{ y: 0 }} // จุดเริ่มต้นที่ y = 0
              animate={{ y: -150 }} // เคลื่อนขึ้นไปที่ -150px
              transition={{
                duration: 0.8, // ระยะเวลาการเคลื่อนที่
                repeat: 1, // จำนวนครั้งที่เด้ง
                repeatType: "reverse", // เด้งกลับลงมาที่ y = 0
                ease: "easeInOut", // ความนุ่มนวลของการเคลื่อนที่
              }}
              style={{
                position: "absolute",
                left: "50%",
                transform: "translateX(-50%)",
                bottom: "10px",
                width: "50px",
              }}
            />
          )}
        </motion.div>
      )}

      {/* แสดงผลลัพธ์เลขเซียมซี */}
      {stickNumber && (
        <p style={{ marginTop: "20px", fontSize: "24px", color: "red" }}>
          ใบเซียมซีหมายเลข {stickNumber}
        </p>
      )}
    </div>
  );
};

export default GameSeemsee;
