import React from "react";

const MatterPortPage = () => {
  const styles = {
    container: {
      width: "100vw", // เต็มความกว้างหน้าจอ
      height: "100vh", // เต็มความสูงหน้าจอ
      margin: 0,
      padding: 0,
      overflow: "hidden", // ป้องกัน scrollbars
    },
    iframe: {
      width: "100%", // เต็มความกว้างของ container
      height: "100%", // เต็มความสูงของ container
      border: "none", // ไม่มีขอบ iframe
    },
  };

  return (
    <div style={styles.container}>
      <iframe
        style={styles.iframe}
        src="https://my.matterport.com/show/?m=VBv2MTshKxC"
        allowFullScreen
        mozAllowFullScreen
        webkitAllowFullScreen
        allow="xr-spatial-tracking; clipboard-write"
      ></iframe>
    </div>
  );
};

export default MatterPortPage;
