import React, { createContext, useState, useContext } from 'react';

// สร้าง Context สำหรับการจัดการภาษา
const LanguageContext = createContext();

export const useLanguage = () => useContext(LanguageContext);

// สร้าง LanguageProvider เพื่อให้การเปลี่ยนภาษาใช้ได้ทั่วทั้งแอป
export const LanguageProvider = ({ children }) => {
  const [language, setLanguage] = useState('en'); // ค่าเริ่มต้นเป็นภาษาอังกฤษ

  const toggleLanguage = () => {
    setLanguage((prevLang) => (prevLang === 'en' ? 'th' : 'en')); // สลับภาษา
  };

  return (
    <LanguageContext.Provider value={{ language, toggleLanguage }}>
      {children}
    </LanguageContext.Provider>
  );
};
