import React, { useState,useEffect } from "react";
import { useLanguage } from '../components/LanguageContext';
import {faPhone,faEnvelope,faClock,faGlobe,faCircleChevronRight} from '@fortawesome/free-solid-svg-icons';
import { faFacebook,faInstagram,faLine } from '@fortawesome/free-brands-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

import locationIcon from '../assets/locationIcon.png';
import qrCode from '../assets/qrCode.png';
import { height, width } from "@fortawesome/free-solid-svg-icons/fa0";
import { color } from "framer-motion";



const ContactUsPage = () => {
    const { language } = useLanguage('th');
    const [isMobile, setIsMobile] = useState(false);
    const iframeWidth = isMobile ? '100%' : '80%';

    useEffect(() => {
        window.addEventListener('resize', handleResize);
        handleResize(); // Initial check
        return () => window.removeEventListener('resize', handleResize);
      }, []);
    
      const handleResize = () => {
        setIsMobile(window.innerWidth <= 1200);
      };

    const styles = {
        Page: {
            paddingTop:isMobile ? '100px' : '150px',
            width: '100%',          // Change width to 100% to use the full screen width
            boxSizing: 'border-box',
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center',
            margin: '0 auto',  // Center content horizontally
            gap:'16px',
            boxSizing:'border-box'
        },
        content: {
            display: 'flex',
            flexDirection: isMobile ? 'column' : 'row',  // จัดเรียงแนวตั้งในโทรศัพท์
            width: isMobile ? '100%' : '95%',  // กำหนดความกว้าง
            justifyContent: isMobile ? 'center' : 'space-between', // จัดให้อยู่ตรงกลางในโทรศัพท์
            alignItems: isMobile ? 'center' : 'flex-start', // จัดให้อยู่ตรงกลางแนวตั้งในโทรศัพท์
            padding: isMobile ? '0px 16px' : '0px',
            boxSizing: 'border-box'
        },
        contentLeft:{
            display:'flex',
            textAlign:'left',
            width:isMobile ? '100%' :'70%',  // Allow left content to take up 48% of the width
            flexDirection: 'column',
            justifyContent:'center',
            flexDirection: 'column', // หรือ 'row' หากต้องการเรียงแนวนอน
            alignItems: 'center',
            boxSizing:'border-box'
        },
        contentRight:{
            borderTopLeftRadius:'30px',
            borderTopRightRadius:'30px',
            padding:'72px 16px',
            background:'#2e5853',
            display: 'flex',
            alignItems: 'center', 
            flexDirection: 'column',   
            textAlign: 'left',
            gap:'10px',
            width:isMobile ? '100%' :'50%',
            boxSizing:'border-box',
            marginTop:isMobile ? '0' :'52px'
        },
        Details:{
            color:'#666666',
            fontFamily:  language === 'en' ?  "'Poppins', sans-serif" :  "'Kanit', sans-serif",
            justifyContent: 'center', 
            width: '100%',
        },
        Title:{
            fontFamily:  language === 'en' ? "'Cormorant Garamond', serif" :  "'Kanit', sans-serif",
            color:'#2e5853',
            fontSize:isMobile ? '48px' :'72px',
            margin:0,
            width: '100%', // Ensure title spans full width
            borderBottom: '1px solid #AD9468',
            fontWeight:'bold'
        },
        contactDetails:{
            display:'flex',
            alignItems:'center',
            gap:'24px',
            fontFamily:"'Kanit', sans-serif",
            fontSize:'16px'
        },
        designTop:{
           padding:'16px 24px'
        },
        contentScan:{
            background:'white',
            width:isMobile ? '80%' :'60%',
            borderRadius:'24px',
            fontFamily:  language === 'en' ?  "'Poppins', sans-serif" :  "'Kanit', sans-serif",
            fontSize:'32px',
            textAlign:'center',
            color:'#2e5853',
            fontWeight:'bold'
        },
        learnMoreButton: {
            marginTop: '20px',
            padding: '16px 32px', // ปรับ padding ให้มีขนาดกว้างขึ้น
            backgroundColor: 'white',
            color: '#10443e',
            border: '1px solid #10443e',
            borderRadius: '1000px',
            cursor: 'pointer',
            fontSize: '18px',
            boxShadow: '0px 4px 16px 0px #10443E33',
            fontFamily: language === 'en' ? "'Poppins', sans-serif" : "'Kanit', sans-serif",
            fontWeight: 'bold',
            alignItems: 'center',
            justifyContent: 'center', // ปรับให้ข้อความอยู่กลางปุ่ม
            display: 'flex',
            textAlign: 'center', // เพื่อให้ข้อความอยู่ตรงกลาง
            gap:'16px',
            width:'100%'
          },
          AddLine:{
            width:isMobile ? '100%' :'60%',
          },
          contentLeftDetails:{
            width:isMobile ? '100%' :'70%',
          }
        }

    return(
        <div style={styles.Page}>
            <div style={styles.content}>
                <div style={styles.contentLeft}>
                  <div style={styles.contentLeftDetails}>
                  <div style={styles.Details}>
                            {language === 'en' ? (
                                <div style={styles.DetailText}>
                                    <p style={styles.Title}>Contact us</p>
                                    <div style={styles.designTop}>
                                       <div style={{...styles.contactDetails,padding:'8px 0'}}>
                                            <img src={locationIcon} alt="locationIcon" 
                                            style={{width:'18px'}}/>
                                            <a 
                                            href="https://maps.app.goo.gl/A2QtXEadmwjpErDP8" 
                                            target="_blank" 
                                            rel="noopener noreferrer"
                                            style={{
                                                fontFamily:  "'Kanit', sans-serif",
                                                color:'#666666',
                                                
                                            }} 
                                            >
                                                68/7 หมู่ 3 ตำบลบางกอบัว อำเภอ​พระประแดง จังหวัดสมุทรปราการ 10130
                                            </a>
                                       </div>
                                       <div style={styles.contactDetails}>
                                            <FontAwesomeIcon 
                                                icon={faPhone} 
                                                style={{ 
                                                    fontSize: '18px', 
                                                    color:'#2e5853',
                                                   
                                                }}
                                            />
                                           <p style={{
                                                 fontFamily: "'Poppins', sans-serif",
                                                 margin:0,
                                                 padding:'8px 0'
                                            }}>092 505 4994</p>
                                       </div>
                                       <div style={styles.contactDetails}>
                                            <FontAwesomeIcon 
                                                icon={faEnvelope} 
                                                style={{ 
                                                    fontSize: '18px', 
                                                    color:'#2e5853',
                      
                                                }}
                                            />
                                            <p style={{
                                                 fontFamily: "'Poppins', sans-serif",
                                                 margin:0,
                                                 padding:'8px 0'
                                            }}>info.learndo@gmail.com</p>
                                       </div>
                                       <div style={styles.contactDetails}>
                                            <FontAwesomeIcon 
                                                icon={faClock} 
                                                style={{ fontSize: '18px', color:'#2e5853',}}
                                            />
                                            <p
                                            style={{
                                                fontFamily: "'Poppins', sans-serif",
                                                margin:0,
                                                padding:'8px 0'
                                            }}                                            
                                            >Open 9:00–18:00 Closed on Saturday and Sunday</p>
                                       </div>
                                    </div>
                                    <div style={styles.designTop}>
                                        <div style={{...styles.contactDetails,padding:'8px 0'}}>
                                            <FontAwesomeIcon 
                                                icon={faGlobe} 
                                                style={{ fontSize: '18px', color:'#2e5853', }}
                                            />
                                            <a 
                                            href="https://learndo.my.canva.site/learndo-information-web" 
                                            target="_blank" 
                                            rel="noopener noreferrer"
                                            style={{
                                                fontFamily: "'Poppins', sans-serif",
                                                color:'#666666',
                                                margin:0,
                                                padding:'8px 0'                                            }} 
                                            >
                                            https://learndo.my.canva.site/learndo-information-web
                                            </a>
                                       </div>
                                       <div style={styles.contactDetails}>
                                            <FontAwesomeIcon 
                                                icon={faFacebook} 
                                                style={{ fontSize: '18px', color:'#2e5853', }}
                                            />
                                            <p style={{
                                                margin:0,
                                                padding:'8px 0',
                                                fontFamily: "'Poppins', sans-serif",
                                                }}>LearnDo</p>
                                       </div>
                                       <div style={styles.contactDetails}>
                                            <FontAwesomeIcon 
                                                icon={faInstagram} 
                                                style={{ fontSize: '18px', color:'#2e5853',}}
                                            />
                                            <p style={{
                                                 fontFamily: "'Poppins', sans-serif",margin:0,padding:'8px 0'
                                            }}>learndo.club</p>
                                       </div>
                                       <div style={styles.contactDetails}>
                                            <FontAwesomeIcon 
                                                icon={faLine} 
                                                style={{ fontSize: '18px', color:'#2e5853',}}
                                            />
                                            <p
                                            style={{
                                                fontFamily: "'Poppins', sans-serif",margin:0,padding:'8px 0'
                                            }}                                            
                                            >@learndo</p>
                                       </div>
                                    </div>
                                </div>
                            ) : (
                                <div style={styles.DetailText}>
                                <p style={styles.Title}>ติดต่อ</p>
                                <div style={styles.designTop}>
                                   <div style={{...styles.contactDetails,padding:'8px 0'}}>
                                        <img src={locationIcon} alt="locationIcon" 
                                        style={{width:'18px'}}/>
                                        <a 
                                        href="https://maps.app.goo.gl/A2QtXEadmwjpErDP8" 
                                        target="_blank" 
                                        rel="noopener noreferrer"
                                        style={{
                                            fontFamily:  "'Kanit', sans-serif",
                                            color:'#666666',
                                            
                                        }} 
                                        >
                                            68/7 หมู่ 3 ตำบลบางกอบัว อำเภอ​พระประแดง จังหวัดสมุทรปราการ 10130
                                        </a>
                                   </div>
                                   <div style={styles.contactDetails}>
                                        <FontAwesomeIcon 
                                            icon={faPhone} 
                                            style={{ 
                                                fontSize: '18px', 
                                                color:'#2e5853',
                                               
                                            }}
                                        />
                                       <p style={{
                                             fontFamily: "'Poppins', sans-serif",
                                             margin:0,
                                             padding:'8px 0'
                                        }}>092 505 4994</p>
                                   </div>
                                   <div style={styles.contactDetails}>
                                        <FontAwesomeIcon 
                                            icon={faEnvelope} 
                                            style={{ 
                                                fontSize: '18px', 
                                                color:'#2e5853',
                  
                                            }}
                                        />
                                        <p style={{
                                             fontFamily: "'Poppins', sans-serif",
                                             margin:0,
                                             padding:'8px 0'
                                        }}>info.learndo@gmail.com</p>
                                   </div>
                                   <div style={styles.contactDetails}>
                                        <FontAwesomeIcon 
                                            icon={faClock} 
                                            style={{ fontSize: '18px', color:'#2e5853',}}
                                        />
                                        <p
                                        style={{
                                            fontFamily: "'Poppins', sans-serif",
                                            margin:0,
                                            padding:'8px 0'
                                        }}                                            
                                        >Open 9:00–18:00 Closed on Saturday and Sunday</p>
                                   </div>
                                </div>
                                <div style={styles.designTop}>
                                    <div style={{...styles.contactDetails,padding:'8px 0'}}>
                                        <FontAwesomeIcon 
                                            icon={faGlobe} 
                                            style={{ fontSize: '18px', color:'#2e5853', }}
                                        />
                                        <a 
                                        href="https://learndo.my.canva.site/learndo-information-web" 
                                        target="_blank" 
                                        rel="noopener noreferrer"
                                        style={{
                                            fontFamily: "'Poppins', sans-serif",
                                            color:'#666666',
                                            margin:0,
                                            padding:'8px 0'                                            }} 
                                        >
                                        https://learndo.my.canva.site/learndo-information-web
                                        </a>
                                   </div>
                                   <div style={styles.contactDetails}>
                                        <FontAwesomeIcon 
                                            icon={faFacebook} 
                                            style={{ fontSize: '18px', color:'#2e5853', }}
                                        />
                                        <p style={{
                                            margin:0,
                                            padding:'8px 0',
                                            fontFamily: "'Poppins', sans-serif",
                                            }}>LearnDo</p>
                                   </div>
                                   <div style={styles.contactDetails}>
                                        <FontAwesomeIcon 
                                            icon={faInstagram} 
                                            style={{ fontSize: '18px', color:'#2e5853',}}
                                        />
                                        <p style={{
                                             fontFamily: "'Poppins', sans-serif",margin:0,padding:'8px 0'
                                        }}>learndo.club</p>
                                   </div>
                                   <div style={styles.contactDetails}>
                                        <FontAwesomeIcon 
                                            icon={faLine} 
                                            style={{ fontSize: '18px', color:'#2e5853',}}
                                        />
                                        <p
                                        style={{
                                            fontFamily: "'Poppins', sans-serif",margin:0,padding:'8px 0'
                                        }}                                            
                                        >@learndo</p>
                                   </div>
                                </div>
                            </div>
                            )}
                        </div>
                        <div style={{padding:'16px 24px'}}>
                        <iframe
                            src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d3876.3783667514485!2d100.57228617573085!3d13.695519098592847!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x30e29f7f0cced80f%3A0x26e748da162e9ff3!2sLearnDo!5e0!3m2!1sth!2sth!4v1733901756673!5m2!1sth!2sth"
                            width={iframeWidth} // ใช้ตัวแปรสำหรับความกว้าง
                            height="200px"
                            style={{ border: 0, borderRadius: '24px' }}
                            allowFullScreen=""
                            loading="lazy"
                            referrerPolicy="no-referrer-when-downgrade"
                            title="Google Map"
                        />
                        </div>
                  </div>
                </div>
                <div style={styles.contentRight}>
                    <div style={styles.contentScan}>
                        <img src={qrCode} alt="qrCode" style={{width:'100%',borderRadius:'24px'}}/>
                        <p>SCAN ME</p>
                    </div>
                    <div style={styles.AddLine}>
                    <button 
                        style={{
                            ...styles.learnMoreButton,
                            display: 'flex',
                            alignItems: 'center',
                            justifyContent: 'center',
                            position: 'relative',
                        }}
                        onClick={() => window.open('https://www.line.me/th/', '_blank')}
                    >
                        <span style={{
                            flex: 1, 
                            textAlign: 'center'
                        }}>
                            {language === 'en' ? 'ADD LINE' : 'ADD LINE'}
                        </span>
                        <FontAwesomeIcon 
                            icon={faCircleChevronRight} 
                            style={{
                                fontSize: '18px',
                                position: 'absolute',
                                right: '16px',
                            }}
                        />
                    </button>
                    </div>
                </div>
            </div>
        </div>
    )
};

export default ContactUsPage;
