import React, { useEffect, useState } from 'react';
import { Link, useLocation } from 'react-router-dom';
import logo from '../assets/logo.png';
import languageIcon from '../assets/languageIcon.png';
import { useLanguage } from '../components/LanguageContext';
import { width } from '@fortawesome/free-brands-svg-icons/fa42Group';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faBars, faXmark } from '@fortawesome/free-solid-svg-icons';
import { color } from 'framer-motion';

const Topbar = () => {
  const { language, toggleLanguage } = useLanguage('th');
  const [isMobile, setIsMobile] = useState(false);
  const [isMenuOpen, setIsMenuOpen] = useState(false);
  const location = useLocation(); 

  useEffect(() => {
    window.addEventListener('resize', handleResize);
    handleResize(); // Initial check
    return () => window.removeEventListener('resize', handleResize);
  }, []);

  const handleResize = () => {
    setIsMobile(window.innerWidth <= 1200);
  };

  const toggleMenu = () => {
    setIsMenuOpen(!isMenuOpen);
  };

  const styles = {
    topbarStyle: {
      top: 0,
      display: 'flex',
      width: location.pathname === '/' ? (isMobile ? '100%' : 'auto') : '100%',
      justifyContent: 'space-between',
      alignItems: 'center',
      padding: '16px',
      position: 'fixed',
      background: isMobile ? '#f5f5f5' : 'white',
      zIndex: 1,
      gap: '16px',
    },
    logoContainer: {
      position: isMobile ? 'absolute' : 'static',
      left: '50%',
      transform: isMobile ? 'translateX(-50%)' : 'none',
      textAlign: 'center',
      display: 'flex',
      alignItems: 'center',
      gap: '16px',
    },
    logo: {
      height: isMobile ? '63px' : '80px',
      objectFit: 'contain',
      marginRight: isMobile ? '30px' : '0',
    },
    menuPage: {
      display: isMobile ? (isMenuOpen ? 'flex' : 'none') : 'flex',
      flexDirection: isMobile ? 'column' : 'row',
      position: isMobile ? 'absolute' : 'static',
      width: isMobile ? '60%' : '100%',
      top: '100%', 
      left: 0,
      backgroundColor: 'white',
      boxShadow: isMobile ? '0 4px 6px rgba(0,0,0,0.1)' : 'none',
      zIndex: 1,
      height: isMobile ? '100vh' : '100%',
      alignItems: 'center',
      fontFamily:  language === 'en' ? "'Cormorant Garamond', serif" :  "'Kanit', sans-serif",
       transition: 'transform 0.70s ease-in-out', // เพิ่มการเคลื่อนไหว
      transform: isMobile
        ? isMenuOpen
          ? 'translateX(0)' // เมนูเปิดจะเลื่อนไปที่ตำแหน่งปกติ
          : 'translateX(-100%)' // เมนูปิดจะเลื่อนไปซ้าย
        : 'none', // สำหรับหน้าจอใหญ่ไม่ใช้อนิเมชั่น
        fontWeight:language === 'en' ? 'bold' :  'normal'
    },
    menuItem: {
      padding: '16px',
      fontSize: '24px',
      color: '#10443e',
      textDecoration: 'none',
      textAlign: 'center',
    },
    activeMenuItem: {
      textDecoration: 'underline', // เพิ่มเส้นขีดใต้เมนูที่เลือก
      color: '#10443e', // กำหนดสีให้เมนูที่เลือก
    },
    hamburgerIcon: {
      display: isMobile ? 'block' : 'none',
      fontSize: '32px',
      cursor: 'pointer',
      color: '#10443e',
    },
    languageButton: {
      padding: isMobile ? '30px 0px' :'8px 16px',
      display: 'flex',
      alignItems: 'center',
      background: 'none',
      border: 'none',
      gap: '8px',
      fontSize: '20px',
      fontWeight: 'bold',
      color: '#10443e',
      cursor: 'pointer',
      borderTop: isMobile ? '1px solid #10443e' : 'none',
      width:isMobile ? '90%' :'auto',
      justifyContent: isMobile ? 'center' :'flex-start'
    },
    imgLanguage: {
      width: '20px',
    },
  };

  return (
    <div style={styles.topbarStyle}>
      {/* Hamburger Icon */}
      <div style={styles.hamburgerIcon} onClick={toggleMenu}>
        <FontAwesomeIcon icon={isMenuOpen ? faXmark : faBars} /> {/* เปลี่ยนไอคอนตามสถานะเมนู */}
      </div>

      {/* Logo */}
      <div style={styles.logoContainer}>
        <Link to="/">
          <img src={logo} alt="logo" style={styles.logo} />
        </Link>
        <p
          style={{
            margin: '0',
            fontWeight: 'bold',
            color: '#10443e',
            display: isMobile ? 'none' : 'block',
          }}
        >
          LEARNDO
        </p>
      </div>

      {/* Menu */}
      <div style={styles.menuPage}>
        <Link 
            to="/" 
            style={location.pathname === '/' ? { ...styles.menuItem, ...styles.activeMenuItem } : styles.menuItem}
          >
            {language === 'en' ? 'Home' : 'หน้าแรก'}
          </Link>
          <Link 
            to="/about" 
            style={location.pathname === '/about' ? { ...styles.menuItem, ...styles.activeMenuItem } : styles.menuItem}
          >
            {language === 'en' ? 'About' : 'เกี่ยวกับ'}
          </Link>
          <Link 
            to="/product" 
            style={location.pathname === '/product' ? { ...styles.menuItem, ...styles.activeMenuItem } : styles.menuItem}
          >
            {language === 'en' ? 'Product' : 'สินค้า'}
          </Link>
          <Link 
            to="/contacts" 
            style={location.pathname === '/contacts' ? { ...styles.menuItem, ...styles.activeMenuItem } : styles.menuItem}
          >
            {language === 'en' ? 'Contacts' : 'ติดต่อ'}
          </Link>

        {/* Language Button */}
        <button onClick={toggleLanguage} style={styles.languageButton}>
          <img src={languageIcon} alt="languageIcon" style={styles.imgLanguage} />
          {language === 'en' ? 'TH' : 'EN'}
        </button>
      </div>
    </div>
  );
};

export default Topbar;
